@import "../../styles/utils/mixins";

.home {
  @include contentPaddingInline;
  &__section {
    margin-bottom: 80px;
  }

  &__banner {
    display: flex;
    justify-content: center;
  }
}

.categories {
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-content: space-between;
    gap: 16px;
    margin-top: 24px;

    width: 100%;

    @include onTablet {
      flex-direction: row;
    }
  }

  &__block {
    width: 100%;

    @include onTablet {
      width: 33%;
    }
  }

  &__image {
    width: 100%;
    height: 30vh;

    background-size: cover;
    background-repeat: no-repeat;
    margin-bottom: 24px;

    transition: transform 0.3s ease-in-out;

    &:hover {
      transform: scale(1.03);
    }

    @include onTablet {
      height: 40vh;
    }

    @include onDesktop {
      height: 50vh;
    }

    &-1 {
      background-color: #000;
      background-image: url(../../icons/images/category-phone.jpeg);
      background-position: center;
    }

    &-2 {
      background-color: #8d8d92;
      background-image: url(../../icons/images/category-tablets_new.webp);
    }

    &-3 {
      background-color: #973d5f;
      background-image: url(../../icons/images/category-accessories.png);
    }
  }
}
